import React,{useState} from 'react'
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Adminlogin() {
    const [email,Setemail] = useState("");
    const [password,Setpassword] = useState("")
    const [message,Setmessage] = useState("")
    const navigate = useNavigate();

    function Login(){
        axios.post("http://localhost:8000/adminlogin",{email: email,password: password})
        .then((response)=>{
            if(response.data.length !== 0){
                Setmessage("Login sucessfull")
                navigate('/movie_upload')
            }
            else{
                Setmessage("Login faild")
            }

        })

    }
    setTimeout(()=>{
        Setmessage("")

    },3000)

  
  return (
   <>
    <h2>Admin Login page</h2>
    <input type="email" placeholder='Enter your email' onChange={(e)=>Setemail(e.target.value)}/><br/>
    <input type="password" placeholder='Enter your password' onChange={(e)=>Setpassword(e.target.value)}/><br/>
    <button onClick={Login}>Login</button> 
    <h3>{message}</h3>
   </>
  )
}

export default Adminlogin