
import React, { useEffect, useState } from 'react'
import './Style.css'






function TicketBook() {
  const total = document.getElementById("total")
  
    function getSeat(){
      // SetmovieName(e.target.value)
      // Setdate(e.target.value)
        
        const container = document.querySelector(".container")
        const seats = document.querySelectorAll('.row .seat:not(.occupied)')
        const count = document.getElementById("count")
        
       
       
        
        const movieSelect = document.getElementById("movie");
        

        populateUI()
        let ticketPrice = parseInt(movieSelect.value)
      

        // update total and count:
        function updateSelectedCount(){
          const selectedSeats = document.querySelectorAll('.row .selected')
          
      

         
          
          const seatsIndex = [...selectedSeats].map((seat)=>{
            return [...seats].indexOf(seat);
            
            
          });

         localStorage.setItem('selectedSeats',JSON.stringify(seatsIndex));
          // console.log(selectedSeats)
          const selectedSeatsCount = selectedSeats.length;
          
          count.innerText = selectedSeatsCount;
         
          total.innerHTML =    selectedSeatsCount*ticketPrice
          
         }
         
        // get the data from the local storage
        function populateUI(){
          const selectedSeats = localStorage.getItem('selectedSeats')
           if(selectedSeats!== null && selectedSeats.length>0){
            seats.forEach((seat,index)=>{
              if(selectedSeats.indexOf(index) > -1){
                seat.classList.add('selected')
              }
            })

           }

        

        }

        // movie select event
        movieSelect.addEventListener('change',(e)=>{
          ticketPrice = parseInt(e.target.value);
          
          updateSelectedCount()
        })
       

        container.addEventListener('click',(e)=>{
         if(e.target.classList.contains('seat')  && !e.target.classList.contains('occupied')){
            e.target.classList.toggle('selected')
         }
         updateSelectedCount()
     })

    
        


    }

  

    
//rzp_test_eFjC3VAXXIxA7I
    function payment(){
      let total =document.getElementById("total").innerHTML
      let amount = +total;
     
      
      
      


         var options = {
        "key": "rzp_test_zebxCbiyvBpOYZ", // Enter the Key ID generated from the Dashboard
        "amount": amount*100, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
        "currency": "INR",
        "name": "BookMy show", 
        "description": "Booked the movie now ",
        "image": "https://w7.pngwing.com/pngs/3/952/png-transparent-logo-bookmyshow-business-brand-india-business-text-people-logo-thumbnail.png",
      };
    const paymetObject = new window.Razorpay(options);
    paymetObject.open();
  

  
    

 }




    
   
   
   
  


    
     
    const [allmovie,Setallmovie] = useState([]);


    useEffect(()=>{
        getMovieDetails()
       
    },[]);

     const getMovieDetails = async()=>{
        let result = await fetch("http://localhost:8000/getAllMovie")
        result = await result.json();
        
        Setallmovie(result)
        
  }

    
  return (
    <>
    <h2>Please select your seats</h2>
    <div className='movie-container'>
    <label>Pick the movie</label>
 <select id='movie' onChange={getSeat} >
 <option>Select the movie</option>
    {
        allmovie.map((ele,index)=>{
            return(
                <>
                
                <option value={ele.ticketPrice} key={index} >{ele.title}(Rs.{ele.ticketPrice}) </option>
                </>
            )
        })
    }
 </select>
</div>




<div>
  <ul className="showcase">
    <li>
      <div className="seat" />
      <small>N/A</small>
    </li>
    <li>
      <div className=" seat selected" />
      <small>Selected</small>
    </li>
    <li>
      <div className="seat occupied" />
      <small>Occupied</small>
    </li>
  </ul>
  <div className="container">
      <div className="screen" />
      <div className="row">
      <div className="seat" >1</div>  
      <div className="seat">2</div>  
      <div className="seat">3</div>  
      <div className="seat">4</div>  
      <div className="seat occupied">5</div>  
      <div className="seat">6</div>  
      <div className="seat">7</div>  
      <div className="seat">8</div>  
    </div>
    <div className="row">
      <div className="seat">9</div>  
      <div className="seat">10</div>  
      <div className="seat occupied">11</div>  
      <div className="seat">12</div>  
      <div className="seat">13</div>  
      <div className="seat">14</div>  
      <div className="seat">15</div>  
      <div className="seat">16</div>  
    </div>
      <div className="row">
      <div className="seat">17</div>  
      <div className="seat occupied">18</div>  
      <div className="seat">19</div>  
      <div className="seat">20</div>  
      <div className="seat occupied">21</div>  
      <div className="seat">22</div>  
      <div className="seat">23</div>  
      <div className="seat">24</div>  
    </div>
    <div className="row">
      <div className="seat">25</div>  
      <div className="seat occupied">26</div>  
      <div className="seat">27</div>  
      <div className="seat">28</div>  
      <div className="seat">29</div>  
      <div className="seat">30</div>  
      <div className="seat occupied">31</div>  
      <div className="seat">32</div>  
    </div>
    <div className="row">
      <div className="seat">33</div>  
      <div className="seat">34</div>  
      <div className="seat">35</div>  
      <div className="seat">36</div>  
      <div className="seat occupied">37</div>  
      <div className="seat">38</div>  
      <div className="seat">39</div>  
      <div className="seat">40</div>  
    </div>
    <div className="row">
      <div className="seat">41</div>  
      <div className="seat">42</div>  
      <div className="seat">43</div>  
      <div className="seat">44</div>  
      <div className="seat">45</div>  
      <div className="seat">46</div>  
      <div className="seat">47</div>  
      <div className="seat">48</div>  
    </div>
  </div>
  <p className="text">
    You have selected <span id="count">0</span> seats for a price of Rs.<span id="total">0</span>
  </p>
  
 <button onClick={payment}>Pay Now</button>

 
</div>

</>

    
  )
  
}

export default TicketBook