
import { Box, Button, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react'
import MovieItem from './MovieItem';
import { Link } from 'react-router-dom';


function MovieDisplay() {
    const [movieDetails,SetmovieDetails] = useState([]);
    useEffect(()=>{
       
        getMovieDetails()
    },[]);



    const getMovieDetails = async ()=>{
      
        let result = await fetch("http://localhost:8000/getAllMovie");
        result = await result.json();
        SetmovieDetails(result);
        

    }
  return (
    <>
    <Box width={'100%'} height={'100%'} margin={'auto'} marginTop={2}>
        <Box margin={'auto'} width={'80%'} height={'40vh'} padding={8}>
            <img src="https://m.media-amazon.com/images/M/MV5BNWU5ZDRmMWItZGU0NC00NzZjLTgzYjctY2RlMzI3OTNkN2U5XkEyXkFqcGdeQXVyMTE0MTY2Mzk2._V1_.jpg" alt="Animal" height={'400px'} width={'100%'}/>

        </Box>
        <Box padding={15} margin={'auto'}>
            <Typography variant='h4' textAlign={'center'}>Latest Releases</Typography>

        </Box>
        <Box display={'flex'} width={'80%'} justifyContent={'space-around'} flexWrap={'wrap'}>
            {movieDetails.slice(0,4).map((ele,index)=><MovieItem id={ele._id} title={ele.title} posterUrl={ele.posterUrl} releaseDate={ele.releaseDate} key={index}/>)}
       </Box>
       <Box display="flex" padding={5} margin={'auto'}>
        <Button LinkComponent={Link} to="/all_movies" variant='outlined' sx={{margin: "auto",color: "#2b2d42"}}>View All Movies</Button>

       </Box>


    </Box>
    </>

  )  
}



export default MovieDisplay