import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';





function SignUp() {
    const [name,Setname] = useState("")
    const [email,Setemail] = useState("");
    const [password,Setpassword] = useState("");
    const [message,Setmessage] = useState("");
    const navigate = useNavigate();

     const Submit = async(e)=>{
        e.preventDefault();
        const mydata  = {
            "name": name,
            "email": email,
            "password": password

        }

        const requestOptions ={
            method: 'POST',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(mydata)

        }
        const response = await fetch("http://localhost:8000/signup",requestOptions)
        const data = await response.json();
        console.log(data._id)
        localStorage.setItem("userId", data._id)

        if(data._id != null){
            Setmessage("Registration Sucessfull")
            navigate("/userlogin")
        }
        else{
            Setmessage("Registration faild")
        }
     }
     setTimeout(()=>{
        Setmessage("")


     },3000)
    
  return (

    
   <>
    
    <h2>Sign up</h2>
  <form onSubmit={Submit}>
  <div className="mb-3">
    <label htmlFor="exampleInputEmail1" className="form-label">Name</label>
    <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e)=>Setname(e.target.value)} />
  </div>

  <div className="mb-3">
    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e)=>Setemail(e.target.value)} />
  </div>

  <div className="mb-3">
    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
    <input type="password" className="form-control" id="exampleInputPassword1" onChange={(e)=>Setpassword(e.target.value)} />
  </div>

  <button type="submit" className="btn btn-primary">Submit</button>
</form>
<h3>{message}</h3>

    </>
    
    
  )
}

export default SignUp
