import React, { useEffect, useState } from 'react'
import {AppBar, Toolbar,Box,Autocomplete,TextField, Tabs,Tab} from '@mui/material'
import MovieIcon from '@mui/icons-material/Movie';
import axios from 'axios';
import { Link } from 'react-router-dom';

function Navbar() {
    const [movies,Setmovies] = useState([]);
    const [value,Setvalue] = useState(0);
    useEffect(()=>{
        axios.get("http://localhost:8000/getAllMovie")
        .then((response)=>{
            Setmovies(response.data)
           

        })
    },[]);
  return (
    <>
    <AppBar sx={{backgroundColor:"#2b2d42"}}>
        <Toolbar>
            <Box width={'20%'}>
             <MovieIcon/>
            </Box>
            <Box width={'30%'} margin={'auto'}>
            <Autocomplete
        freeSolo
        options={movies.map((option) => option.title)}
        renderInput={(params) => <TextField sx={{ input:{color:"white"}}} variant='standard' {...params} placeholder="Search Movies" />}
      />
        </Box>
        <Box display={'flex'}>
            <Tabs textColor='inherit' indicatorColor='secondary' value={value} onChange={(e,val)=>Setvalue(val)}>
                <Tab LinkComponent={Link} to="/movie_display"label="Movies"/>
                <Tab LinkComponent={Link} to="/signup" label="Signup"/>
                <Tab LinkComponent={Link} to="/userlogin" label="Login"/>
                <Tab LinkComponent={Link} to="/adminsignup" label="AdminSignup"/>
                <Tab LinkComponent={Link} to="/adminlogin" label="AdminLogin"/>
            </Tabs>

        </Box>
        </Toolbar>
    </AppBar>
    </>
  )
}

export default Navbar