import { Box,  Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import MovieItem from './MovieItem';



function Allmovies() {
    const [allmovie,Setallmovie] = useState([]);
    useEffect(()=>{
        getMovieDetails()
    
     },[]);

     const getMovieDetails = async()=>{
        let result = await fetch("http://localhost:8000/getAllMovie")
        result = await result.json();
        
        Setallmovie(result)
     }
     
  return (
    <>
    <Box margin={'auto'} marginTop={10}>
    <Typography margin={'auto'} variant='h4' padding={2} textAlign={'center'} width={'40%'} bgcolor={"#900C3F"} color={'white'}>All movies</Typography>
    <Box width={'100%'} margin={'auto'} marginTop={5} display={'flex'} justifyContent={'center'} flexWrap={'wrap'}>
        {
            allmovie.map((ele,index)=><MovieItem key={index} id={ele._id} posterUrl={ele.posterUrl} releaseDate={ele.releaseDate} title={ele.title}/>)
            
        }

    </Box>
    

    </Box>
    </>
  )
}

export default Allmovies