import React, { useState,useEffect } from 'react'
import { Link } from 'react-router-dom';
import axios from 'axios';


function AdminDisplay() {
    const [movieDetails,SetmovieDetails] = useState([]);
    const [message,Setmessage] = useState("");
    

    useEffect(()=>{
        getMovieDetails()
    },[]);


    const getMovieDetails = async ()=>{
        let result = await fetch("http://localhost:8000/getAllMovie");
        result = await result.json();
        SetmovieDetails(result);
        

        
    }

    const deleteDate = (id)=>{
        alert("are you sure to delete?")
        axios.delete(`http://localhost:8000/delete/${id}`)
        .then((response)=>{
            getMovieDetails()
            Setmessage("Document deleted sucessfully")

        })
    }


    setTimeout(()=>{
        Setmessage("")
    },3000)
    
  return (
    <>
   
    <div id='data'>
    <h3 className='text'>Movie display for admin</h3>
    <table border={2}>
        <thead>
            <tr>
                <th>Movie</th>
                <th>Description</th>
                <th> Price</th>
                <th>Actors</th>
                <th colSpan={2}>Action</th>
             </tr>
        </thead>
        <tbody>
          
          {
                movieDetails.map((ele)=>{
                    return(
                        
                        
                            <tr>
                        <td>{ele.title}</td>
                        <td>{ele.description}</td>
                        <td>{ele.ticketPrice}</td>
                        <td>{ele.actors}</td>
                        <td><button onClick={()=>deleteDate(ele._id)}>Delete</button></td>
                        <th><button><Link to={"/update/"+ ele._id}>Edit</Link></button></th>
                        </tr>
                        
                        
                        
                    )
                })
            }
          
        </tbody>
    </table>
    </div>
    <h3>{message}</h3>
     </>
  )
}

export default AdminDisplay