import { Box, Button, FormLabel, TextField, Typography } from '@mui/material';
import axios from 'axios';
import React, { Fragment, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'


function Booking() {
    const id = useParams().id;
    console.log(id)
    const [movieId,SetmovieId] = useState({});
    const [inputs,Setinputs] = useState({date: ""});

    const handelChange = (e)=>{
        Setinputs((prevState)=>({...prevState,[e.target.name] : e.target.value}))
    }

    const handelSubmit = (e)=>{
        e.preventDefault();
        console.log(inputs);
     

    }

    useEffect(()=>{
        getMovieById(id )

    },[id])
    console.log(movieId)
    const getMovieById = async(id)=>{
        axios.get(`http://localhost:8000/movie/${id}`)
        .then((response)=>{
            
            SetmovieId(response.data)
            
        })
    }

  


    
    
    
 
  return (
    <div>{movieId && <Fragment>
        <Typography padding={5} margin={'auto'} marginTop={4} fontFamily={'fantacy'} variant='h4' textAlign={'center'}>
             Book Tickets of Movie: {movieId.title}
        </Typography>

        <Box display={'flex'} justifyContent={'center'}>
            <Box display={'flex'} justifyContent={'column'} flexDirection={'column'} paddingTop={2} width={'50%'} marginRight={'auto'}>

             <img width={'80%'} height={'300px'} src={movieId.posterUrl} alt={movieId.title}/>

             <Box width={'80%'} marginTop={2} padding={1}>
                <Typography paddingTop={2}>Description: {movieId.description}</Typography>
                <Typography fontWeight={'bold'} marginTop={1}>
                   Starrer: {movieId.actors + " " }
                   {/* Starer: {movieId.actors.map((ele)=>" "+ ele + " ")} */}
                </Typography>
                <Typography fontWeight={'bold'} marginTop={1}>
                    Release Date: {new Date(movieId.releaseDate).toDateString()}
                </Typography>
                <Typography fontWeight={'bold'} marginTop={1}>
                    Ticket Price:{movieId.ticketPrice}
                </Typography>
             </Box>
            </Box>
            <Box width={'50%'} paddingTop={3}>

                <form onSubmit={handelSubmit}>

                    <Box padding={5} margin={'auto'} display={'flex'} flexDirection={'column'}>
                        
                   
                        <FormLabel>Booking Date</FormLabel>
                        <TextField value={inputs.date} onChange={handelChange} name='date' type='date' margin='normal' variant='standard'/>

                        <Button type='submit'LinkComponent={Link} to="/seat_screen" sx={{mt:3}}>Book Now</Button>
                    </Box>

                </form>
            </Box>
        </Box>
        
        </Fragment>}</div>
        
  )
}

export default Booking