import axios from 'axios';
import React,{useEffect, useState} from 'react'
import { useParams } from 'react-router-dom';

function Update() {
    const [title,Settitle] = useState("")
    const [description,Setdescription] = useState("");
    const [releaseDate,SetreleaseDate] = useState("");
    const [posterUrl,SetposterUrl] = useState("");
    const [actors,Setactors] = useState("");
    const [message,Setmessage] = useState("");
    const [ticketPrice,SetticketPrice] = useState()
    const params = useParams();

    useEffect(()=>{
        axios.get(`http://localhost:8000/update/${params.id}`)
        .then((result)=>{
            Settitle(result.data.title)
            Setdescription(result.data.description)
            SetreleaseDate(result.data.releaseDate)
            SetposterUrl(result.data.posterUrl)
            Setactors(result.data.actors)
            SetticketPrice(result.data.ticketPrice)
            

        }).catch((error)=>console.log(error))
    },[]);
    
      const Save = ()=>{
        axios.put(`http://localhost:8000/update/${params.id}`,{title,description,releaseDate,posterUrl,actors,ticketPrice})
        .then((result)=>{
            console.log(result)
            Setmessage("Document updated sucessfully")
        }).catch((error)=>console.log(error))
      }

      setTimeout(()=>{
        Setmessage("")

      },3000)
  return (
    <>
       <h3>Update Movie</h3>
    <label for="title">Enter movie name: </label>
    <input type="text" value={title} id="title" onChange={(e)=>Settitle(e.target.value)}/><br/>
    <label for="date">Enter release date: </label>
    <input type="date" value={releaseDate} id="releaseDate" onChange={(e)=>SetreleaseDate(e.target.value)}/><br/>
    <label for="poster">Enter poster url: </label>
    <input type="url" value={posterUrl}  id="posterUrl" onChange={(e)=>SetposterUrl(e.target.value)}/><br/>
    <label for="actors">Enter movie actors: </label>
    <input type="text" value={actors}  id="actors" onChange={(e)=>Setactors(e.target.value)}/><br/>
    <label for="ticketPrice">Enter ticket price:</label>
    <input type="number" value={ticketPrice} id="ticketPrice" onChange={(e)=>SetticketPrice(e.target.value)}/><br/>
    <label for="description">Enter movie description: </label>
    <textarea id="description" rows={4} cols={12} value={description}  onChange={(e)=>Setdescription(e.target.value)}/><br/>
    <button onClick={Save}>Save</button>
    <h3>{message}</h3>
    </>
  )
}

export default Update