import React, { useState } from 'react'
import axios from 'axios'

import GoogleLogin from 'react-google-login'
import { useNavigate } from 'react-router-dom';


function Login() {
    const [email,Setemail] = useState("");
    const [password,Setpassword] = useState("")
    const [message,Setmessage] = useState("")
    const navigate = useNavigate();

    function Login(){
        axios.post("http://localhost:8000/userlogin",{email: email,password: password})
        .then((response)=>{
            if(response.data.length !== 0){
                Setmessage("Login sucessfull")
                navigate('/movie_display')
            }
            else{
                Setmessage("Login faild")
            }

        })

    }
    setTimeout(()=>{
        Setmessage("")

    },3000)

    const responseSucessGoogle = (response)=>{
        console.log(response)
        Setmessage("Login sucessfull")
        


    }

    const responseErrorGoogle = (response)=>{

    }
  return (
    <>
   <div id='data'> 
   <h2>Login page</h2>
    <input type="email" placeholder='Enter your email' onChange={(e)=>Setemail(e.target.value)}/><br/>
    <input type="password" placeholder='Enter your password' onChange={(e)=>Setpassword(e.target.value)}/><br/>
    <button onClick={Login}>Login</button>  
</div>
<p>or login with google</p>
<GoogleLogin
    clientId="930796817138-m7v37pef2cu8hauina1bl4plbh23nti9.apps.googleusercontent.com"
    buttonText="Login with google"
    onSuccess={responseSucessGoogle}
    onFailure={responseErrorGoogle}
    cookiePolicy={'single_host_origin'}
  />,
     <h3>{message}</h3>
     
    </>
    
  )
}

export default Login