import React,{useState} from 'react'
import { useNavigate } from 'react-router-dom';

function AdminSign() {
    const [name,Setname] = useState("")
    const [email,Setemail] = useState("");
    const [password,Setpassword] = useState("");
    const [message,Setmessage] = useState("");
    const navigate = useNavigate();

     const Submit = async(e)=>{
        e.preventDefault();
        const mydata  = {
            "name": name,
            "email": email,
            "password": password

        }

        const requestOptions ={
            method: 'POST',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(mydata)

        }
        const response = await fetch("http://localhost:8000/adminsignup",requestOptions)
        const data = await response.json();
        console.log(data._id)
        

        if(data._id != null){
            Setmessage("Registration Sucessfull")
            navigate("/adminlogin")
        }
        else{
            Setmessage("Registration faild")
        }
     }
     setTimeout(()=>{
        Setmessage("")


     },3000)
  return (
    <>
    <h4>welcome to admin signup</h4>
     <form onSubmit={Submit}>
        <input type='text' placeholder='enter name' value={name} onChange={(e)=>Setname(e.target.value)}/><br></br>
        <input type='email' placeholder='enter email' value={email} onChange={(e)=>Setemail(e.target.value)}/><br></br>
        <input type='text' placeholder='enter password' value={password} onChange={(e)=>Setpassword(e.target.value)}/>
        <button type='submit'>Submit</button>
     </form>
     <h3>{message}</h3>
    </>
  )
}

export default AdminSign